import LayerSwitcher from "ol-ext/control/LayerSwitcher";

var layerSwitcherControl = new LayerSwitcher({
  groupSelectStyle: "children", // Can be 'children' [default], 'group' or 'none'
  collapsed: false,
  reordering: false,
  activationMode: "click",
});

layerSwitcherControl.on("drawlist", function (item) {
  var preview = item.layer.get("preview");
  if (preview == null) return;
  var img = document.createElement("img");
  img.className = "ol-layerswitcher-layer-icon";
  img.src = item.layer.get("preview");
  item.li.insertBefore(img, item.li.firstChild);
});

function InitializeLegend(map) {
  map.addControl(layerSwitcherControl);
}

export { InitializeLegend };
