import VectorLayer from "ol/layer/Vector";
import VectorSource from "ol/source/Vector";
import { Fill, Stroke, Style } from "ol/style";

import { KMZ } from "../KMZ";
import source from "url:~/Data/pipeline.kmz";
import icon from "url:~/Icons/pipeline_icon.png";

const TITLE = "Gazoduq pipeline";
const INITIAL_OPACITY = 1;

var style = new Style({
  stroke: new Stroke({
    color: "#005ce6",
    width: 3,
  }),
});

var PipelineLayer = new VectorLayer({
  name: TITLE,
  opacity: INITIAL_OPACITY,
  source: new VectorSource({
    url: source,
    format: new KMZ({ extractStyles: false }),
  }),
  preview: icon,
  style: style,
});

export { PipelineLayer };
