import VectorLayer from "ol/layer/Vector";
import VectorSource from "ol/source/Vector";
import GeoJSON from "ol/format/GeoJSON";
import Crop from "ol-ext/filter/crop";
import TileLayer from "ol/layer/Tile";
import WMTS from "ol/source/WMTS";
import WMTSTileGrid from "ol/tilegrid/WMTS";
import { getTopLeft as GetTopLeft } from "ol/extent";
import { GetResolutions, GetMatrixIds } from "../WMTS";
import {
  Projection as WebMercatorProjection,
  ProjectionExtent as WebMercatorProjectionExtent,
} from "../WebMercator";

import source_contour_abitibi from "url:~/Data/AT.json";
import icon from "url:~/Icons/photo_icon.png";

const PYRAMID_COUNT_QUEBEC = 23;
const PYRAMID_COUNT_ONTARIO = 18;

var clipVectorSource = new VectorSource({
  url: source_contour_abitibi,
  projection: "EPSG:4326",
  format: new GeoJSON(),
});

clipVectorSource.on("change", function (evt) {
  var source = evt.target;
  if (source.getState() === "ready") {
    var clipFeature = clipVectorSource.getFeatures()[0];
    var cropQuebec = new Crop({ feature: clipFeature, inner: false });
    var cropOntario = new Crop({ feature: clipFeature, inner: true });
    QuebecAerialPhotoLayer.addFilter(cropQuebec);
    OntarioAerialPhotoLayer.addFilter(cropOntario);
  }
});

var ClipLayer = new VectorLayer({
  source: clipVectorSource,
  style: null,
  displayInLayerSwitcher: false,
});

var OntarioAerialPhotoLayer = new TileLayer({
  minZoom: 13,
  opacity: 0.5,
  title: "Aerial photography",
  source: new WMTS({
    url: "https://services.arcgisonline.com/arcgis/rest/services/World_Imagery/MapServer/WMTS/",
    layer: "World_Imagery",
    matrixSet: "default028mm",
    format: "image/png",
    projection: WebMercatorProjection,
    tileGrid: new WMTSTileGrid({
      origin: GetTopLeft(WebMercatorProjectionExtent),
      resolutions: GetResolutions(PYRAMID_COUNT_ONTARIO),
      matrixIds: GetMatrixIds(PYRAMID_COUNT_ONTARIO),
    }),
    style: "default",
    wrapX: true,
  }),
  displayInLayerSwitcher: false,
});

var QuebecAerialPhotoLayer = new TileLayer({
  minZoom: 13,
  opacity: 0.5,
  title: "Aerial photography",
  source: new WMTS({
    attributions:
      '<a href="https://servicesmatriciels.mern.gouv.qc.ca/erdas-iws/ogc/wmts/Inventaire_Ecoforestier">Gouvernement du Québec</a>',
    url: "https://servicesmatriciels.mern.gouv.qc.ca/erdas-iws/ogc/wmts/Inventaire_Ecoforestier",
    layer: "Inventaire_Ecoforestier",
    matrixSet: "GoogleMapsCompatibleExt2:epsg:3857",
    format: "image/png",
    projection: WebMercatorProjection,
    tileGrid: new WMTSTileGrid({
      origin: GetTopLeft(WebMercatorProjectionExtent),
      resolutions: GetResolutions(PYRAMID_COUNT_QUEBEC),
      matrixIds: GetMatrixIds(PYRAMID_COUNT_QUEBEC),
    }),
    style: "default",
    wrapX: true,
  }),
  preview: icon,
});

QuebecAerialPhotoLayer.on("change:opacity", function (evt) {
  OntarioAerialPhotoLayer.setOpacity(QuebecAerialPhotoLayer.getOpacity());
});

QuebecAerialPhotoLayer.on("change:visible", function (evt) {
  OntarioAerialPhotoLayer.setVisible(QuebecAerialPhotoLayer.getVisible());
});

export { ClipLayer, QuebecAerialPhotoLayer, OntarioAerialPhotoLayer };
