import GeoJSON from "ol/format/GeoJSON";
import VectorLayer from "ol/layer/Vector";
import VectorSource from "ol/source/Vector";
import { Circle, Fill, Stroke, Style, Text } from "ol/style";

import source from "url:~/Data/Projects.json";
import icon from "url:~/Icons/projects_icon.png";

const TITLE = "Projects";
const INITIAL_OPACITY = 1;

function getLabelOffsetX(name) {
  if (name === "Upper-Beaver") {
    return -30;
  }
  if (name === "Granada") {
    return 13;
  } else {
    return 0;
  }
}

function getLabelOffsetY(name) {
  if (name === "Upper-Beaver") {
    return -20;
  }
  if (name === "Granada") {
    return 5;
  } else {
    return -15;
  }
}

var getLabel = function (feature) {
  var name = feature.get("Name");

  return new Text({
    textAlign: "left",
    text: name,
    font: "bold 18px Arial, sans-serif",
    fill: new Fill({ color: "rgba(0, 0, 0, 1)" }),
    stroke: new Stroke({ color: "rgba(255, 255,255, 1)", width: 3 }),
    offsetX: getLabelOffsetX(name),
    offsetY: getLabelOffsetY(name),
  });
};

function styleFunction(feature, resolution) {
  return new Style({
    image: new Circle({
      radius: 8,
      fill: new Fill({ color: "rgba(0, 255, 0, 1)" }),
      stroke: new Stroke({ color: "black", width: 1 }),
    }),
    text: getLabel(feature),
  });
}

var ProjectsLayer = new VectorLayer({
  name: TITLE,
  opacity: INITIAL_OPACITY,
  source: new VectorSource({
    url: source,
    projection: "EPSG:4326",
    format: new GeoJSON(),
  }),
  style: styleFunction,
  preview: icon,
});

export { ProjectsLayer };
