import { ProjectionExtent } from "./WebMercator";
import { getWidth as GetWidth } from "ol/extent";

var size = GetWidth(ProjectionExtent) / 256;

function GetResolutions(pyramidCount) {
  var resolutions = new Array(pyramidCount);
  for (var z = 0; z < pyramidCount; ++z) {
    resolutions[z] = size / Math.pow(2, z);
  }
  return resolutions;
}

function GetMatrixIds(pyramidCount) {
  var matrixIds = new Array(pyramidCount);
  for (var z = 0; z < pyramidCount; ++z) {
    matrixIds[z] = z;
  }
  return matrixIds;
}

export { GetResolutions };
export { GetMatrixIds };
