import Overlay from "ol/Overlay";
import Select from "ol/interaction/Select";

var _map;
var selectInteraction = new Select({
  layers: function (layer) {
    return (
      layer.get("identifiable") === undefined ||
      layer.get("identifiable") == true
    );
  },
});

var container = document.getElementById("popup");
var content = document.getElementById("popup-content");
var closer = document.getElementById("popup-closer");

var overlay = new Overlay({
  element: container,
  autoPan: true,
  autoPanAnimation: {
    duration: 250,
  },
});

closer.onclick = function () {
  overlay.setPosition(undefined);
  closer.blur();
  selectInteraction.getFeatures().clear();

  return false;
};

var OnMapSingleClick = function (evt) {
  selectInteraction.getFeatures().clear();

  var features = [];
  _map.forEachFeatureAtPixel(evt.pixel, function (feature, layer) {
    if (
      layer.get("identifiable") === undefined ||
      layer.get("identifiable") == true
    ) {
      feature.set("layer", layer);
      features.push(feature);
    }
  });

  if (features.length == 0) {
    overlay.setPosition(undefined);
    closer.blur();
    selectInteraction.getFeatures().clear();
    return;
  }

  var selectedFeature = features[0];
  var coordinate = evt.coordinate;
  var properties = selectedFeature.getProperties();

  var htmlContent = "";
  if (
    properties.hasOwnProperty("name") &&
    properties.hasOwnProperty("description")
  ) {
    htmlContent = properties.description;
  } else {
    htmlContent += '<table class="feature_attributes">';
    for (const property in properties) {
      if (property == "geometry" || property == "layer") continue;
      htmlContent += `<tr><td>${property}</td><td>${properties[property]}</td></tr>`;
    }

    htmlContent += "</table>";
  }

  var title = `<h1>${selectedFeature.get("layer").get("name")}</h1>`;

  content.innerHTML = title + htmlContent;

  content.querySelectorAll("table tr").forEach(function (e, i) {
    if (e.textContent.trim().length == 0) {
      // if row is empty
      e.parentNode.removeChild(e);
    }
  });

  overlay.setPosition(coordinate);
  selectInteraction.getFeatures().push(selectedFeature);
};

function InitializeIdentify(map) {
  _map = map;
  map.addOverlay(overlay);
  map.addInteraction(selectInteraction);
  map.on("singleclick", OnMapSingleClick);
}

export { InitializeIdentify };
