import VectorLayer from "ol/layer/Vector";
import VectorSource from "ol/source/Vector";

import { KMZ } from "../KMZ";
import source from "url:~/Data/Timiskaming_consult_dd_Layer.kmz";
import icon from "url:~/Icons/tfn_territory_icon.png";

const TITLE = "TFN territory";
const INITIAL_OPACITY = 1;

var TFNTerritoryLayer = new VectorLayer({
  name: TITLE,
  opacity: INITIAL_OPACITY,
  source: new VectorSource({
    url: source,
    format: new KMZ(),
  }),
  preview: icon,
  identifiable: false,
});

export { TFNTerritoryLayer };
