import "ol/ol.css";
import "ol-ext/dist/ol-ext.css";

import { InitializeLegend } from "./Legend";
import { InitializeIdentify } from "./Identify";
import { InitializeScale } from "./Scale";

import { PrivateLandLayer } from "./layers/PrivateLand";
import {
  AnnualCutQuebecLayer,
  AnnualCutOntarioLayer,
} from "./layers/AnnualCut";
import { PipelineLayer } from "./layers/Pipeline";
import { TFNTerritoryLayer } from "./layers/TFNTerritory";
import { ProjectsLayer } from "./layers/Projects";
import { ClipLayer } from "./layers/AerialPhoto";
import { QuebecAerialPhotoLayer } from "./layers/AerialPhoto";
import { OntarioAerialPhotoLayer } from "./layers/AerialPhoto";
import { WorldStreetMapLayer } from "./layers/WorldStreetMap";

import Map from "ol/Map";
import View from "ol/View";

var view = new View({
  center: [-8823588, 6013538],
  zoom: 8,
});

var layers = [
  ClipLayer,
  WorldStreetMapLayer,
  OntarioAerialPhotoLayer,
  QuebecAerialPhotoLayer,
  TFNTerritoryLayer,
  PrivateLandLayer,
  AnnualCutQuebecLayer,
  AnnualCutOntarioLayer,
  PipelineLayer,
  ProjectsLayer,
];

var map = new Map({
  layers: layers,
  target: "map",
  view,
});

InitializeLegend(map);
InitializeIdentify(map);
InitializeScale(map);
