import TileLayer from "ol/layer/Tile";
import WMTS from "ol/source/WMTS";
import WMTSTileGrid from "ol/tilegrid/WMTS";
import { getTopLeft as GetTopLeft } from "ol/extent";
import { GetResolutions, GetMatrixIds } from "../WMTS";
import {
  Projection as WebMercatorProjection,
  ProjectionExtent as WebMercatorProjectionExtent,
} from "../WebMercator";

const PYRAMID_COUNT = 18;

var WorldStreetMapLayer = new TileLayer({
  displayInLayerSwitcher: false,
  source: new WMTS({
    url: "https://server.arcgisonline.com/arcgis/rest/services/World_Street_Map/MapServer/WMTS/1.0.0",
    layer: "World_Street_Map",
    matrixSet: "default028mm",
    format: "image/jpeg",
    projection: WebMercatorProjection,
    tileGrid: new WMTSTileGrid({
      origin: GetTopLeft(WebMercatorProjectionExtent),
      resolutions: GetResolutions(PYRAMID_COUNT),
      matrixIds: GetMatrixIds(PYRAMID_COUNT),
    }),
    style: "default",
    wrapX: true,
  }),
});

export { WorldStreetMapLayer };
