import VectorLayer from "ol/layer/Vector";
import VectorSource from "ol/source/Vector";

import { KMZ } from "../KMZ";
import source from "url:~/Data/terres_privees_LayerToKML.kmz";
import icon from "url:~/Icons/private_land_icon.png";

const TITLE = "Private land";
const INITIAL_OPACITY = 0.5;

var PrivateLandLayer = new VectorLayer({
  name: TITLE,
  opacity: INITIAL_OPACITY,
  source: new VectorSource({
    url: source,
    overlaps: false,
    format: new KMZ(),
  }),
  preview: icon,
});

export { PrivateLandLayer };
