import VectorLayer from "ol/layer/Vector";
import VectorSource from "ol/source/Vector";
import { Fill, Stroke, Style } from "ol/style";

import { KMZ } from "../KMZ";
import sourceQuebec from "url:~/Data/cut_8251.kmz";
import sourceOntario from "url:~/Data/harvest2021a.kmz";
import icon from "url:~/Icons/annual_cut_icon.png";

const TITLE = "Annual cut 2021-22";
const INITIAL_OPACITY = 0.5;

var style = new Style({
  fill: new Fill({
    color: "red",
  }),
  stroke: new Stroke({
    color: "red",
    width: 1,
  }),
});

var AnnualCutQuebecLayer = new VectorLayer({
  name: TITLE,
  opacity: INITIAL_OPACITY,
  source: new VectorSource({
    url: sourceQuebec,
    overlaps: false,
    format: new KMZ({
      extractStyles: false,
    }),
  }),
  preview: icon,
  style: style,
});

var AnnualCutOntarioLayer = new VectorLayer({
  name: TITLE,
  displayInLayerSwitcher: false,
  opacity: INITIAL_OPACITY,
  source: new VectorSource({
    url: sourceOntario,
    overlaps: false,
    format: new KMZ({
      extractStyles: false,
    }),
  }),
  preview: icon,
  style: style,
});

AnnualCutQuebecLayer.on("change:opacity", function (evt) {
  AnnualCutOntarioLayer.setOpacity(AnnualCutQuebecLayer.getOpacity());
});

AnnualCutQuebecLayer.on("change:visible", function (evt) {
  AnnualCutOntarioLayer.setVisible(AnnualCutQuebecLayer.getVisible());
});

export { AnnualCutQuebecLayer, AnnualCutOntarioLayer };
